import styled from "@emotion/styled";

export const ButtonGeneral = styled.button`
  padding: 14px;
  background-color: black;
  font-size: 15px;
  border-radius: 3rem;
  color: white;
  border: 1px solid rgb(97, 32, 158);
  cursor: pointer;
  &:hover {
    color: white;
    transition: 1s;
  }
`;
